import React, { useContext, useState } from 'react'
import { Tabs, Tab, Form, Button, Alert } from 'react-bootstrap'
import { OutboundLink } from 'gatsby-plugin-google-analytics'
import fetchHandler from '../../utils/fetchHandler'
import formatPrice from '../../utils/formatPrice'
import payirImage from './payir.png'
import loading from '../Icon/loading.gif'
import ProductDetail from '../ProductDetail/ProductDetail'
import CodeForm from '../CodeForm/CodeForm'
import MyApp from '../../contexts/MyApp'

const TabsInModalToBuy = ({ title, description, price, image }) => {
  const app = useContext(MyApp)
  const [isLoadingCoupon, setIsLoadingCoupon] = useState(false)
  const [isLoadingGetToken, setIsLoadingGetToken] = useState(false)

  const [responseCouponAPI, setResponseCouponAPI] = useState(null)
  const [responseGetTokenAPI, setResponseGetTokenAPI] = useState(null)

  const [coupon, setCoupon] = useState(null)
  const [token, setToken] = useState(null)
  const [discount, setDiscount] = useState(null)

  const handleChangeCoupon = (event) => {
    if (event) event.preventDefault()
    setCoupon(event.target.value)
  }

  const handleSubmitCoupon = async (event) => {
    if (event) event.preventDefault()
    setIsLoadingCoupon(true)

    try {
      const result = await fetchHandler({
        method: 'POST',
        url: '/api/v1/code/check',
        body: {
          code: coupon,
        },
        auth: true,
      })
      if (result.data.success) {
        setDiscount(result.data.result.discount)
        setResponseCouponAPI({
          type: 'success',
          message: `%${result.data.result.discount}   `,
        })
      } else {
        setResponseCouponAPI({
          type: 'danger',
          message: '   ',
        })
        setDiscount(null)
      }
    } catch (e) {
      console.error(e)
    }
    setIsLoadingCoupon(false)
  }

  const handleGetToken = async (event) => {
    if (event) event.preventDefault()
    setIsLoadingGetToken(true)

    try {
      const result = await fetchHandler({
        method: 'POST',
        url: '/api/v1/pay/prepare',
        body: {
          processName: app.process.processName,
          code: coupon || null,
        },
        auth: true,
      })
      if (result.data.success) {
        setToken(result.data.response.token)
        setResponseGetTokenAPI({
          type: 'success',
          message: `     `,
        })
        setTimeout(() => {
          window.location.href = `https://pay.ir/pg/${result.data.response.token}`
        }, 1000)
      } else {
        setResponseGetTokenAPI({
          type: 'danger',
          message: '   ',
        })
        setDiscount(null)
      }
    } catch (e) {
      console.error(e)
    }
    setIsLoadingGetToken(false)
  }

  let newPrice = discount
    ? (Number(price) * (100 - Number(discount))) / 100
    : price
  newPrice = formatPrice(newPrice)

  const oldPrice = formatPrice(price)

  return (
    <div className="buy-body">
      <ProductDetail
        title={title}
        image={image}
        description={description}
        discount={discount}
        oldPrice={oldPrice}
        newPrice={newPrice}
      />
      <CodeForm
        isLoadingCoupon={isLoadingCoupon}
        loading={loading}
        handleSubmitCoupon={handleSubmitCoupon}
        responseCouponAPI={responseCouponAPI}
        handleChangeCoupon={handleChangeCoupon}
        coupon={coupon}
      />
      <div className="tabs-buy">
        {(app.process.processName === 'react-advanced' ||
          app.process.processName === 'react-basic') && (
          <Tabs defaultActiveKey="transfer" id="uncontrolled-tab-example">
            <Tab eventKey="transfer" title="">
              <div className="tab-buy">
                 
                {` `}
                {!discount && `${oldPrice} `}
                {` `}
                {discount && (
                  <>
                    <strike>{oldPrice}</strike> {newPrice} 
                  </>
                )}
                {` `}
                      
                <div className="buy-number">6104 3374 9981 1279</div>
                <div className="buy-name"> </div>
                       
                <div className="tab-buy-actions">
                  <OutboundLink
                    rel="noopener noreferrer"
                    className="btn btn-success"
                    target="_blank"
                    href="https://t.me/ehsangazar"
                  >
                    
                  </OutboundLink>
                </div>
              </div>
            </Tab>
            {/* <Tab eventKey="payir" title=" ">
            <div className="tab-buy payir-section">
              <div className="payir-name">
                <div className="payir-loading">
                  {isLoadingGetToken && (
                    <img className="loading" src={loading} alt="loading" />
                  )}
                </div>
                <div>
                  <img src={payirImage} alt="payir" />
                </div>
                <div>    pay.ir</div>
              </div>
              <div className="payir-button">
                <Button
                  variant="primary"
                  type="submit"
                  className="widthAll"
                  onClick={handleGetToken}
                  disabled={isLoadingGetToken}
                >
                  {isLoadingGetToken && '  '}
                  {!isLoadingGetToken && '   '}
                </Button>
              </div>
            </div>
          </Tab> */}
            <Tab eventKey="paypal" title="">
              <div className="tab-buy">
                      Paypal   
                         
                <OutboundLink
                  rel="noopener noreferrer"
                  target="_blank"
                  href="https://www.paypal.me/ehsangazar"
                >
                  {' '}
                  {' '}
                </OutboundLink>
                      
                <a href="https://t.me/ehsangazar">  </a>
                  .
              </div>
            </Tab>
          </Tabs>
        )}
        {app.process.processName === 'javascript' && (
          <Tabs defaultActiveKey="transfer" id="uncontrolled-tab-example">
            <Tab eventKey="transfer" title="">
              <div className="tab-buy">
                 
                {` `}
                {!discount && `${oldPrice} `}
                {` `}
                {discount && (
                  <>
                    <strike>{oldPrice}</strike> {newPrice} 
                  </>
                )}
                {` `}
                      
                <div className="buy-number">6037 6974 7422 9348</div>
                <div className="buy-name">  -   </div>
                       
                <div className="tab-buy-actions">
                  <OutboundLink
                    rel="noopener noreferrer"
                    className="btn btn-success"
                    target="_blank"
                    href="https://t.me/ehsangazar"
                  >
                    
                  </OutboundLink>
                </div>
              </div>
            </Tab>
            <Tab eventKey="abroad" title="  ">
              <div className="tab-buy">
                 
                {` `}
                {!discount && `${oldPrice} `}
                {` `}
                {discount && (
                  <>
                    <strike>{oldPrice}</strike> {newPrice} 
                  </>
                )}
                {` `}
                      
                <div className="buy-info">
                  <div className="buy-abroad">BSB Number: 193879</div>
                  <div className="buy-abroad">Account Number: 449109401</div>
                  <div className="buy-abroad">
                    Account Name: Yarra Foundation Inc.
                  </div>
                  <div className="buy-abroad">Bank of Melbourne</div>
                </div>
                       
                <div className="tab-buy-actions">
                  <OutboundLink
                    rel="noopener noreferrer"
                    className="btn btn-success"
                    target="_blank"
                    href="https://t.me/ehsangazar"
                  >
                    
                  </OutboundLink>
                </div>
              </div>
            </Tab>
          </Tabs>
        )}
      </div>
    </div>
  )
}

export default TabsInModalToBuy
